import React from "react"

import Layout from "../components/layout"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import styles from "./index.module.css"
import SEO from "../components/seo"

export default ({ data }) => (
  <Layout>
    <div className={styles.main_h}>
      <SEO title="About" />
      <div className={styles.content}>
        <h1>About</h1>
        <p>Yao majored in physics during college and studied for a master's degree in electrical engineering at National Taiwan University. In this period, he cooperated with TSMC to research semiconductor related fields, and published his works in AIP Advances as the first author. He also tries to explore more possibilities of human and machine cooperation in art. 
        </p>
        <p>In addition, the sketch 《Most beautiful equation in math》 has been invited and published in 《Science Monthly》 in Taiwan. In the future, he hopes to use what he has learned to merge the spirit of science and cutting-edge technology into art, our society, and our daily life.
        </p>
      </div>
      <div className={styles.image}>
        <Img fluid={data.file.childImageSharp.fluid} />
      </div>
    </div>
  </Layout>
)

export const query = graphql`
  query {
    file(relativePath: {eq: "about.jpg"}) {
      childImageSharp {
        fluid(maxWidth: 500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`